<template>
  <div class="user-mangement">
    <div class="search-box">
      <gs-search
        v-model="keyword"
        placeholder="请输入姓名或帐号"
        @clear="fetchUserList(1)"
        @search="fetchUserList"
      />
    </div>
    <gs-table
      v-loading="loading"
      :data="tableData"
      :pagination="pagination"
      style="width: 100%"
      paging
      @size-change="sizeChange"
      @page-change="pageChange"
    >
      <gs-table-column
        prop="account"
        label="帐号"
      />
      <gs-table-column
        prop="username"
        label="姓名"
      />
      <gs-table-column
        prop="roles"
        label="角色"
      >
        <template slot-scope="scope">
          <gs-table-tags
            :tags="scope.row.roles"
            :show-count="2"
          />
        </template>
      </gs-table-column>
      <gs-table-column
        label="操作"
        width="100"
        fixed="right"
      >
        <template slot-scope="scope">
          <gs-button
            type="text-primary"
            @click="checkDetail(scope)"
          >详情</gs-button>
        <!-- <gs-button
          type="text-primary"
        >编辑</gs-button> -->
        </template>
      </gs-table-column>
    </gs-table>
  </div>
</template>
<script>
import './style';
import * as service from 'src/service/auth/user';
export default {
  data() {
    return {
      keyword: '',
      tableData: [],
      pagination: {
        total: 0,
        pageSize: 10,
        pageSizes: [10, 20, 30],
        current: 1
      },
      loading: false
    };
  },
  computed: {

  },

  created() {
    this.fetchUserList(1);
  },

  methods: {
    fetchUserList(isfirstPage = 0) {
      if (isfirstPage === 1) this.pagination.current = 1;
      this.loading = true;
      service.getUserList({
        offset: (this.pagination.current - 1) * this.pagination.pageSize,
        limit: this.pagination.pageSize,
        query: this.keyword
      }).then(data => {
        data.lists.map(item => {
          item.roles = [item.roleName];
          return item;
        });
        this.tableData = data.lists;
        this.pagination.total = data.sum;
        this.loading = false;
      }).catch(e => {
        this.loading = false;
      });
    },
    pageChange(current) {
      this.pagination.current = current;
      this.fetchUserList();
    },
    sizeChange(size) {
      this.pagination.pageSize = size;
      this.fetchUserList(1);
    },
    checkDetail({row}) {
      row.userId && this.$router.push(`/main/auth/userManagement/${row.userId}`);
    }
  }
};
</script>



